#ImageDeGarde-Container{
    width: 100vw;
}

#ImageDeGarde-Image{
    width: 100vw;
    position: absolute;
    top:0;
}

#TitreDebutDePage-grid {
    display: grid;
    grid-template-areas: "a b";
    justify-content: center;
    align-items: center;
    position: relative;
}

#TitreDebutDePage-UAlogo{
    grid-area: a;
}

#TitreDebutDePage-texte{
    grid-area: b;
    color:white;
    font-weight: lighter;
}

#TitreDebutDePage-h1{
    font-family: "Poiret One", serif;
    font-weight: lighter;
}

#TitreDebutDePage-h2{
    font-family: "Roboto Thin", sans-serif;
    font-weight: lighter;
    margin: 0;
}

@media only screen and (max-width: 767px) {
    #ImageDeGarde-Container{
        height: 190px;
    }
    #ImageDeGarde-Image{
        left: -10vw;
    }

    #TitreDebutDePage-grid {
        grid-template-columns: 80px auto;
        grid-gap: 15px;
        padding-top: 50px;
        margin-left: -20vw;
    }

    #TitreDebutDePage-h1{margin-bottom: -10px; margin-top: -5px}

    #TitreDebutDePage-UAlogo{width: 80px;}
    #TitreDebutDePage-h1{font-size: 15px;}
    #TitreDebutDePage-h2{font-size: 10px;}
}

@media only screen and (min-width: 768px) {
    #ImageDeGarde-Container{
        height: 800px;
    }
    #ImageDeGarde-Image{
        left: -19vw;
    }

    #TitreDebutDePage-grid {
        grid-template-columns: 200px auto;
        grid-gap: 30px;
        padding-top: 200px;
        margin-left: -38vw;
    }

    #TitreDebutDePage-h1{margin-bottom: 0; margin-top: -7px}

    #TitreDebutDePage-UAlogo{width: 200px;}
    #TitreDebutDePage-h1{font-size: 40px;}
    #TitreDebutDePage-h2{font-size: 30px;}
}